import { ApplicationRef, ComponentFactoryResolver, ComponentRef, EmbeddedViewRef, Inject, Injectable, Injector, OnDestroy, Type } from '@angular/core';
import { DOCUMENT } from '@angular/common';

import { FabricDialogComponent } from './fabric-dialog.component';
import { fromEvent, Subject } from 'rxjs';
import { filter, takeUntil } from 'rxjs/operators';

@Injectable()
export class FabricDialogService implements OnDestroy {

	dialogRef: ComponentRef<any> = null;

	private readonly destroy$ = new Subject();

	constructor(private componentFactoryResolver: ComponentFactoryResolver,
				private applicationRef: ApplicationRef,
				private injector: Injector,
				@Inject(DOCUMENT) private document: any) {
	}

	ngOnDestroy() {
		this.removeDialog();
	}

	open(component: Type<any>, injector?: Injector): void {
		if (!this.dialogRef) {
			this.createAndAppend(component, injector);
			this.closeOnEscKey();
		}
	}

	close(): void {
		this.removeDialog();
	}

	private closeOnEscKey(): void {
		const close$ = fromEvent<any>(this.document, 'keyup');

		close$
			.pipe(
				filter((key) => key.code === 'Escape'),
				takeUntil(this.destroy$))
			.subscribe(() => this.close());
	}

	private createAndAppend(component: any, injector?: Injector): void {

		const compInjector = injector || this.injector;

		const componentRef = this.componentFactoryResolver
								 .resolveComponentFactory(FabricDialogComponent)
								 .create(compInjector);

		componentRef.instance.dialogNestedComponent = component;
		componentRef.changeDetectorRef.detectChanges();

		this.applicationRef.attachView(componentRef.hostView);

		const domDialogElement = (componentRef.hostView as EmbeddedViewRef<any>)
			.rootNodes[0] as HTMLElement;

		this.document.body.appendChild(domDialogElement);

		this.dialogRef = componentRef;
	}

	private removeDialog(): void {
		if (this.dialogRef) {
			this.applicationRef.detachView(this.dialogRef.hostView);
			this.dialogRef.destroy();
			this.dialogRef = null;
			this.destroy$.next();
			this.destroy$.complete();
		}
	}

}
