import { ElementRef, Injectable, Injector, Type } from '@angular/core';
import { Observable } from 'rxjs';

import { FabricInlineDialogService } from '../../layout/inline-dialog/fabric-inline-dialog.service';
import { InlineDialogPlacement } from '../../layout/inline-dialog/placement';

@Injectable()
export class FabricDatePickerInlineDialogService {

	constructor(private readonly fabricInlineDialogService: FabricInlineDialogService) {
	}

	open(element: ElementRef, component: Type<any>, injector?: Injector, placement?: InlineDialogPlacement, offset?: number): void {
		this.fabricInlineDialogService.open(element, component);
	}

	close(): void {
		this.fabricInlineDialogService.close();
	}

	isOpened(): boolean {
		return this.fabricInlineDialogService.isOpened();
	}

	onOpened(): Observable<boolean> {
		return this.fabricInlineDialogService.onOpened();
	}

}
