import {
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	ElementRef,
	EventEmitter,
	Input,
	OnDestroy,
	OnInit,
	Output,
	SimpleChanges,
	ViewChild,
	ViewEncapsulation
} from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Subject, Subscription } from 'rxjs';
import { distinctUntilChanged, map, skip, take, takeUntil } from 'rxjs/operators';

import { FabricDatePickerCalendarComponent } from './calendar/fabric-date-picker-calendar.component';
import { FabricDatePickerService } from './fabric-date-picker.service';
import { FabricDatePickerInlineDialogService } from './fabric.date-picker-inline-dialog.service';


@Component({
	selector: 'gui-date-picker',
	templateUrl: './fabric-date-picker.component.html',
	styleUrls: [
		'./fabric-date-picker.ngx.scss',
		'./themes/fabric-date-picker.dark.ngx.scss'
	],
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class FabricDatePickerComponent implements OnInit, OnDestroy {

	@ViewChild('datePicker', { static: false })
	datePickerRef: ElementRef;

	@Input()
	selectDate: Date;

	@Input()
	name: string;

	@Input()
	openDialog: boolean = false;

	@Input()
	onlyDialog: boolean = false;

	@Output()
	dateSelected = new EventEmitter();

	@Output()
	dialogOpened = new EventEmitter();

	datePickerForm: FormGroup;

	pickedDate: Date;

	inputDisabled: 'disabled' | '';

	datePickerSubscription: Subscription;
	datePickerDaySubscription: Subscription;

	private readonly unsub$ = new Subject();

	constructor(private readonly fabricDatePickerInlineDialogService: FabricDatePickerInlineDialogService,
				private readonly datePickerService: FabricDatePickerService,
				private readonly formBuilder: FormBuilder,
				private readonly changeDetectorRef: ChangeDetectorRef) {

		this.datePickerForm = formBuilder.group({
				'date': ['']
			}
		);
	}

	ngOnChanges(changes: SimpleChanges) {
		if (changes.selectDate) {
			this.datePickerService.dateSelected(this.selectDate);
		}

		if (changes.onlyDialog !== null) {
			this.inputDisabled = this.onlyDialog ? 'disabled' : '';
		}
	}

	ngOnInit() {
		this.datePickerSubscription =
			this.datePickerService
				.observeSelectedDate()
				.pipe(
					take(1),
					distinctUntilChanged(),
					takeUntil(this.unsub$)
				)
				.subscribe(
					(date: Date) => {
						this.pickedDate = date;
						this.dateSelected.emit(date);
					}
				);

		this.datePickerService
			.observeSelectedDate()
			.pipe(
				skip(1),
				takeUntil(this.unsub$)
			)
			.subscribe(
				(date: Date) => {
					this.pickedDate = date;
					this.changeDetectorRef.detectChanges();
					this.dateSelected.emit(date);
					this.fabricDatePickerInlineDialogService.close();
				}
			);

		this.fabricDatePickerInlineDialogService
			.onOpened()
			.pipe(
				skip(1),
				takeUntil(this.unsub$)
			)
			.subscribe((opened: boolean) => {
				this.dialogOpened.emit(opened);
			});


		this.observeDayChanges();
	}

	ngAfterViewInit() {
		if (this.openDialog) {
			this.openDatePicker();
		}
	}

	ngOnDestroy() {
		this.fabricDatePickerInlineDialogService.close();

		this.datePickerSubscription.unsubscribe();
		this.datePickerDaySubscription.unsubscribe();

		this.unsub$.next();
		this.unsub$.complete();
	}

	openDatePicker() {
		this.fabricDatePickerInlineDialogService.open(this.datePickerRef, FabricDatePickerCalendarComponent);
	}

	private observeDayChanges(): void {
		this.datePickerDaySubscription =
			this.datePickerForm
				.controls['date']
				.valueChanges
				.pipe(
					distinctUntilChanged(),
					// debounceTime(1000),
					map((day: string) => this.parse(day)),
					takeUntil(this.unsub$)
				)
				.subscribe(
					(day: Date) => {
						this.datePickerService.dateSelected(day);
					});
	}

	private parse(value: string): Date {
		if ((typeof value === 'string') && (value.includes('/'))) {
			const str = value.split('/');

			const year = +str[2],
				month = +str[1] - 1,
				date = +str[0];

			return new Date(year, month, date);
		} else return this.pickedDate;
	}

}

