import {
	AfterViewInit,
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	ComponentFactoryResolver,
	ElementRef,
	forwardRef,
	Inject,
	Type,
	ViewChild,
	ViewContainerRef,
	ViewEncapsulation
} from '@angular/core';

import { FabricDialogService } from './fabric-dialog.service';

@Component({
	templateUrl: './fabric-dialog.component.html',
	styleUrls: [
		'./fabric-dialog.ngx.scss',
		'./themes/fabric-dialog.dark.ngx.scss'
	],
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class FabricDialogComponent implements AfterViewInit {

	@ViewChild('container', { read: ViewContainerRef, static: false })
	container: ViewContainerRef;

	dialogNestedComponent: Type<any>;

	constructor(private componentFactoryResolver: ComponentFactoryResolver,
				private changeDetectorRef: ChangeDetectorRef,
				private elementRef: ElementRef,
				@Inject(forwardRef(() => FabricDialogService)) private dialogService: FabricDialogService) {
	}

	ngAfterViewInit() {
		this.createNestedComponent(this.dialogNestedComponent);
		this.changeDetectorRef.detectChanges();
	}

	closeDialog() {
		this.dialogService.close();
	}

	clickOutside(event: any): void {
		if (this.isContainerClicked(event)) {
			this.dialogService.close();
		}
	}

	private isContainerClicked(event: any): boolean {
		const dialogContentRef = this.elementRef.nativeElement.querySelector('.gui-dialog-content');

		if (dialogContentRef) {
			return !dialogContentRef.contains(event.target);
		}
	}

	private createNestedComponent(component: Type<any>): void {
		const componentFactory = this.componentFactoryResolver.resolveComponentFactory(component);

		this.container.createComponent(componentFactory);
	}
}
